<template>
  <div id="app">
    <h1>Cecile'Noteüòè</h1>
    <p>AppÁªÑ‰ª∂</p>
    <div class="full" @click="full">&loz;ÂÖ®Â±è&loz;</div>
    <router-view></router-view>
    <!-- <img src="./assets/logo.png"> -->
  </div>
</template>
<script>
import screenfull from 'screenfull'
export default {
  name: 'appÁªÑ‰ª∂',
  created () { // ÊØèÊ¨°ÂàáÊç¢ÁªÑ‰ª∂ÊàñÂà∑Êñ∞Âùá‰ºöÊâßË°å
    if (window.Storage && window.localStorage && window.localStorage instanceof Storage) {
      // Âú®È°µÈù¢Âä†ËΩΩÊó∂ËØªÂèñlocalStorage
      if (localStorage.getItem('store')) {
        this.$store.replaceState(Object.assign({}, this.$store.state, JSON.parse(localStorage.getItem('store'))))
      }
      setInterval(() => { // ÊØè10sÊääÁä∂ÊÄÅ‰øùÂ≠òÂà∞localStorage‰∏ÄÊ¨°
        localStorage.setItem('store', JSON.stringify(this.$store.state))
        console.log('saved')
      }, 10000)
      // Âú®È°µÈù¢Âà∑Êñ∞Êó∂Â∞Üstore‰øùÂ≠òÂà∞localStorageÈáå(safariË≤å‰ºº‰∏çÊîØÊåÅbeforeunload)
      window.addEventListener('beforeunload', () => {
        localStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    } else {
      alert('ÊÇ®ÁöÑÊµèËßàÂô® ‰∏çÊîØÊåÅlocalStorageÔºåÂ∞Ü‰ºöÂΩ±Âìç‰ΩøÁî®ÔºÅ')
    }
  },
  methods: {
    full () {
      if (!screenfull.isEnabled) { // Âà§Êñ≠ÊµèËßàÂô®ÊòØÂê¶ÊîØÊåÅscreenfullÊèí‰ª∂
        this.$message({
          message: 'Your browser does not work',
          type: 'warning'
        })
        return false
      }
      screenfull.toggle()
    }
  }
}
</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  border: 1px solid brown;
  overflow: hidden;
}
h1 {
  font: bold 28px/42px arial;
}
.full {
  font: bold 16px/32px arial;
}
</style>



