Vue.config.productionTip = false

// Ê≥®ÂÜådoubletap‰∫ã‰ª∂
var VueTouch = require('vue-touch')
VueTouch.registerCustomEvent('doubletap', {
  type: 'tap',
  taps: 2
})

Vue.use(VueTouch, {name: 'v-touch'})

/* eslint-disable no-new */
new Vue({ // ‰ΩøÁî®vue-routerÊó∂ÔºåË¶ÅÊúÄÂêéÊåÇËΩΩAppÊ†πÂÆû‰æãÔºå‰∏çÁÑ∂‰ºöÂØºËá¥ÈÖçÁΩÆ‰∏çÊàêÂäü!
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) { // ÈúÄË¶ÅÈ™åËØÅÁôªÂΩïÁä∂ÊÄÅÁöÑÈ°µÈù¢Ë∑ØÁî±
    // ËøôÈáåÂà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩïÔºåÈ™åËØÅÊú¨Âú∞Â≠òÂÇ®ÊòØÂê¶Êúâtoken
    if (!store.state.status && to.path !== '/') { // Êú™ÁôªÂΩï,ÂéüË∑ØÁî±Âç¥ÊåáÂêëÈùûÁôªÂΩïÈ°µ
      next('/') // ÈáçÂÆöÂêëÂà∞ÁôªÂΩïÈ°µ
      console.log('11' + store.state.status)
    } else if (store.state.status && to.path === '/') { // Â∑≤ÁôªÂΩï,ÂéüË∑ØÁî±Âç¥ÊåáÂêëÁôªÂΩïÈ°µ
      next('/home/list') // ÈáçÂÆöÂêëÂà∞‰∏ªÈ°µ
      console.log('22' + store.state.status + to.path)
    } else {
      console.log('33' + store.state.status + to.path)
      next() // „ÄêÂøÖÈ°ªÊúâÔºÅÔºÅÔºÅÂê¶ÂàôÂΩì‰∏çÊª°Ë∂≥‰ª•‰∏ä2Êù°‰ª∂ÔºåÈ°µÈù¢Â∞ÜÊó†Ê≥ïËøõË°åËΩ¨Âêë„Äë
    }
  } else {
    console.log('44')
    next() // Á°Æ‰øù‰∏ÄÂÆöË¶ÅË∞ÉÁî® next()
  }
})



