<template>
  <div>
      <div id="register" v-if="isReg">
      <div class="account-login">
        <input id="username-reg" type="text" name="" placeholder="Áî®Êà∑Âêç" v-model="reg_username">
        <input id="password-reg" type="text" name="" placeholder="ÂØÜÁ†Å" v-model="reg_password">
        <p class="tips">ÊèêÁ§∫ÔºöÈ°ªÂêåÊó∂ÂåÖÂê´Â≠óÊØçÂíåÊï∞Â≠óÔºåÊó†ÁâπÊÆäÁ¨¶Âè∑Ôºå‰∏îËá≥Â∞ë6‰Ωç</p>
      </div>
      <p class="add-list-item">
        <button @click="register">Ê≥®ÂÜå</button>
        <!-- <button @click="cancel">ÂèñÊ∂à</button> -->
      </p>
    </div>
    <div id="login" v-else>
      <p>loginÁªÑ‰ª∂</p>
      <div class="account-login">
        <input id="username" type="text" name="" placeholder="Áî®Êà∑Âêç" v-model="username">
        <input id="password" type="text" name="" placeholder="ÂØÜÁ†Å" v-model="password">
        <p class="tips">ÊèêÁ§∫ÔºöÈ°ªÂêåÊó∂ÂåÖÂê´Â≠óÊØçÂíåÊï∞Â≠óÔºåÊó†ÁâπÊÆäÁ¨¶Âè∑Ôºå‰∏îËá≥Â∞ë6‰Ωç</p>
      </div>
      <p class="add-list-item">
        <button  @click="login">ÁôªÂΩï</button>
      </p>
    </div>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
function CheckPassWord (_str) { // ÂøÖÈ°ª‰∏∫Â≠óÊØçÂä†Êï∞Â≠ó‰∏îÈïøÂ∫¶‰∏çÂ∞è‰∫é8‰Ωç
  var str = _str
  if (str == null || str.length < 6) {
    alert('‰∏çËÉΩ‰∏∫Á©∫Ôºå‰∏îËá≥Â∞ë6‰ΩçÔºÅ')
    return false
  }
  var reg1 = new RegExp(/^[0-9A-Za-z]+$/)
  if (!reg1.test(str)) {
    alert('‰∏çËÉΩ Âê´ÊúâÁâπÊÆäÁ¨¶Âè∑ÔºÅ')
    return false
  }
  var reg2 = new RegExp(/[A-Za-z].*[0-9]|[0-9].*[A-Za-z]/)
  if (!reg2.test(str)) {
    alert('ÈúÄË¶Å ÂêåÊó∂ÂåÖÂê´Â≠óÊØçÂíåÊï∞Â≠óÔºÅ')
    return false
  } else {
    return true
  }
}
export default {
  name: 'loginÁªÑ‰ª∂',
  data () {
    return {
      isReg: null,
      reg_username: null,
      reg_password: null,
      username: null,
      password: null
    }
  },
  created () { // Ê†πÊçÆÊòØÂê¶ÊúâÊ≥®ÂÜåËÆ∞ÂΩïÔºåÊ∏≤ÊüìÊ≥®ÂÜå„ÄÅÊàñÁôªÂΩïÈ°µÈù¢
    if (store.state.note_username === '' || store.state.note_password === '') {
      this.isReg = true
    } else {
      this.isReg = false
    }
    return this.isReg
  },
  methods: {
    /* cancel () {
      this.isReg = false
    }, */
    register () {
      if (CheckPassWord(this.reg_username) && CheckPassWord(this.reg_password)) {
        store.commit('register1', {
          regname: this.reg_username,
          regpass: this.reg_password
        })
        this.isReg = false
      }
    },
    login () {
      if (CheckPassWord(this.username) && CheckPassWord(this.password)) {
        if (store.state.note_username === this.username && store.state.note_password === this.password) {
          store.commit('changeStatus')
          this.$router.push('/home/list')
        } else {
          alert('Áî®Êà∑ÂêçÊàñÂØÜÁ†ÅÊúâËØØ')
        }
      }
    }
  }
}
</script>

<style type="text/css" scoped>
#register, #login {
  position: relative;
  height: 300px;
  border: 1px solid green;
  margin-top: 5%;
}
.tips {
  font: bold 12px/18px Ê•∑‰Ωì,arial;
  color: red;
}
.account-login {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 90px;
  width: 60%;
}
#username, #password, #username-reg, #password-reg {
  font: 16px/24px Ê•∑‰Ωì;
  width: 60%;
  border-color: blue;
  border-style: solid;
  border-width: 0 0 1px;
}
#username:focus, #password:focus, #username-reg:focus, #password-reg:focus {
  outline: none;
}
.add-list-item {
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 60%;
  transform: translateX(-50%);
}
.add-list-item button {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  border-width: 0;
  font: 16px/32px Ê•∑‰Ωì;
  text-decoration: none;
}
</style>



